@import url(https://fonts.googleapis.com/css2?family=Antic+Slab&family=Anton&family=Arvo&family=Quicksand&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* outline: 1px dotted green; */
}

:root {
  --color1:#1A2B34 ;
  --color2:#6498CE ;
  --color3:#FBC95A ;
  --color4:#D0824E ;
  --color5:#E1403C ;

  --font1: 'Antic Slab', serif;
  --font2: 'Anton', sans-serif;
  --font3: 'Arvo', serif;
  --font4: 'Quicksand', sans-serif;
}



h1 {
  font-family: 'Antic Slab', serif;
  font-family: var(--font1);
  font-size: 250%;
}

h2 {
  font-family: 'Anton', sans-serif;
  font-family: var(--font2);
  font-size: 28px;
}

body{
  min-height: 100vh;
  background: linear-gradient(0deg, #6498CE 0%, #1A2B34 65%);
  background: linear-gradient(0deg, var(--color2) 0%, var(--color1) 65%);
  /* padding-bottom: 5rem; */
}

html {
  width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.NavBar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--color1);
    border-bottom: 5px solid var(--color3);
    z-index: 1;
}
/* Lists */
.no-style {
    list-style-type: none;
}

/* Font Colors */
.tDark {
    color: var(--color1);
}
.tBlue{
    color: var(--color2);
}
.tYellow {
    color: var(--color3);
}
.tOrange {
    color: var(--color4);
}
.tRed {
    color: var(--color5);
}

/* Background Colors */
.bDark {
    background: var(--color1);
}
.bBlue{
    background: var(--color2);
}
.bYellow {
    background: var(--color3);
}
.bOrange {
    background: var(--color4);
}
.bRed {
    background: var(--color5);
}

.thefamImg {
    border: 1px solid black;
    border-radius: 3rem;
}

hr {
    margin: 0 2rem 1rem 0;
    height: 0.1rem;
    background: var(--color1);
}

.Yborder {
    border: 5px solid var(--color3);
}

ul {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

li {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: var(--font4);
    font-weight: bold;
}

.label {
    display: flex;
    align-items: center;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    text-transform: uppercase;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    max-height: -webkit-min-content;
    max-height: -moz-min-content;
    max-height: min-content;
    color: var(--color3);
}

@media screen and (max-width: 800px){
    .label {
        text-transform: uppercase;
        -webkit-writing-mode: horizontal-tb;
            -ms-writing-mode: lr-tb;
                writing-mode: horizontal-tb;
        justify-content: center;
    }
}
.tech_skills_container {
    margin-top: 53px;
    border-bottom: 5px solid var(--color3);
}

.tech_skills_container li {
    font-size: 150%;
}

.tech_icons {
    display: flex;
    flex-flow: row wrap;
    padding: 1rem 2rem 1rem 0 ;
    background: var(--color5);
    border-top: 3px solid black;
}

.tech_icon {
    margin-left: 0.5rem;
}

.tech_skill_title {
    margin-bottom: 1rem;
    border-bottom: 3px solid black;
    background: var(--color5);

}
.about_me_container {
    margin-top: 3rem;
    padding: 1rem 0;
    background: linear-gradient(0deg, var(--color1) 0%, var(--color2) 100%);
}

.about_me_inner_container {
    display: flex;
    justify-content: center;
}

.about_me_content {
    width: 70%;
    padding: 2rem;
    background: white;
}

.about_me_img {
    width: 70%;
    max-width: 400px;
    margin-bottom: 1rem;
    border-radius: 20px;
    border: 2px solid black;
}

@media screen and (max-width: 800px){
    .about_me_inner_container{
        display: flex;
        flex-flow: column-reverse;
    }
    .about_me_content{
        width: 85%;
    }
}
.card {
    min-width: 300px;
    height: 400px;
    overflow: auto;
}

.card::-webkit-scrollbar {
    display: none;
}

.card_img {
    height: 100px;
    border-radius: 20px;
}

.card-title{
    height: 70px;
    font-size: 200%;
}

.education-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-row {
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 150px;
}

.card_content {
    padding: 0 1rem;
}


@media screen and (max-width: 800px){
    .education-container{
        display: flex;
        flex-flow: column-reverse;
    }
}
.footer_container {
    margin-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--color1);
}

.footer_content {
    margin: 0.5rem 0;
    color: var(--color3);
}
.technical_projects_container{
    padding-top: 1rem;
    background-image: url(/static/media/background.759859ea.png);
    border-top: 5px solid var(--color3);
    border-bottom: 5px solid var(--color3);
}

.tech-box {
    margin: auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    align-items: center;
    width: 90%;
    background: white;
    border: 3px solid var(--color3);
}

.tech-box-content {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact_me_container {
    position: relative;
    top: 53px;
    display: flex;
    justify-content: center;
}
.contact_me_box {
    margin-top: 2rem;
    padding: 1rem;
    width: 70%;
    background: var(--color2);
    border-radius: 20px;
    border: 3px solid var(--color3);
}

@media screen and (min-width: 450px){
    .contact_me_box p {
        font-size: 30px;
    }
}
